import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import {
  Badge,
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  Table,
  Button,
} from "reactstrap";
import axios from "axios";
import usersData from "./UsersData";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function UserRow(props) {
  const user = props.user;
  const userLink = `/users/userInfo/${user._id}`;
  const favorites = `/users/favorites/${user._id}`;
  const comments = `/users/comments/${user._id}`;
  const [change, setChange] = useState(false);

  const getBadge = (status) => {
    return status === "Active"
      ? "success"
      : status === "Inactive"
      ? "secondary"
      : status === "Pending"
      ? "warning"
      : status === "Banned"
      ? "danger"
      : "primary";
  };

  const handleChangeStatusUser = (id) => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        mutation changeStatusUser($userId:ID!){
          changeStatusUser(userId:$userId){
            status,
            message
          }
        }
        `,
        variables: {
          userId: id,
        },
      },
    }).then((res) => {
      const { status, message } = res.data.data.changeStatusUser;
      if (status === 200) {
        toast.success(message);
        setChange(!change);
      } else {
        toast.error(message);
      }
    });
  };

  return (
    <tr key={user._id}>
      <td>
        <Link to={favorites}>
          <Button color="warning">
            <i className="fa fa-heart-o fa-lg"></i>
          </Button>
        </Link>
      </td>
      <td>
        <Link to={comments}>
          <Button color="primary">
            <i className="fa fa-comments fa-lg"></i>
          </Button>
        </Link>
      </td>
      <td>{user.fname}</td>
      <td>
        <Link to={userLink}>{user.phone}</Link>
      </td>
      <td>{user.lname ? user.lname : "****"}</td>
      <td>{user.address ? user.address : "****"}</td>
      <td>{user.number ? user.number : "****"}</td>
      <td>{user.email ? user.email : "****"}</td>
      <td>
        {user.status ? (
          <Badge
            color="success"
            onClick={() => handleChangeStatusUser(user._id)}
            style={{ cursor: "pointer" }}
          >
            تایید شده
          </Badge>
        ) : (
          <Badge
            color="danger"
            onClick={() => handleChangeStatusUser(user._id)}
            style={{ cursor: "pointer" }}
          >
            تایید نشده
          </Badge>
        )}
      </td>
      <td>
        <Link to={userLink}>
          <Badge color={getBadge(user.status)}>{user.status}</Badge>
        </Link>
      </td>
    </tr>
  );
}

const Users = () => {
  const userList = usersData.filter((user) => user.id < 20);
  const [users, setUsers] = useState([]);



  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query alluser($userId:ID,$page:Int,$limit:Int){
          getUsers(userId:$userId,page:$page,limit:$limit){
            _id,
            phone,
            favorite{
              user{
                phone
              },
              product{
                fname
              }
            },
            comment{
              title
            },
            fname,
            address,
            email,
            status
          }
        }
        `,
        variables: {
          userId: null,
          page: 1,
          limit: 30,
        },
      },
    })
      .then((res) => {
        if (res.data.errors) {
          toast.error("خطا در دریافت اطلاعات");
        } else {
          setUsers(res.data.data.getUsers);
        }
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col xl={12}>
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> همه{" "}
              <small className="text-muted">کاربران</small>
            </CardHeader>
            <CardBody>
              <Table responsive hover>
                <thead>
                  <tr>
                    <th scope="col"></th>
                    <th scope="col"></th>
                    <th scope="col">نام کاربر</th>
                    <th scope="col">موبایل</th>
                    <th scope="col">نام خانوادگی</th>
                    <th scope="col">کد پستی</th>
                    <th scope="col">تلفن ثابت</th>
                    <th scope="col">ایمیل</th>
                    <th scope="col">وضعیت</th>
                  </tr>
                </thead>
                <tbody>
                  {users.map((user, index) => (
                    <UserRow key={user._id} user={user} />
                  ))}
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Users;
